.hero .content,header li{text-transform:uppercase}.hero .content,header{font-family:'Open Sans Condensed',helvetica,arial,sans-serif}.animate-link,a,a:link,a:visited{border-bottom:1px solid transparent}header{position:absolute;top:0;width:100%;height:auto;z-index:3}header .header-container{display:flex;margin:0 20px}header .left-align{align-self:flex-start}header .right-align{margin-left:auto}header .secondary-nav{line-height:22px}header .mail-icon{width:15px;height:22px;vertical-align:bottom;margin-right:5px}header li{display:inline-block;height:auto;margin:0 20px}.hero h1,.hero h2{margin-bottom:20px}header li:first-child{margin-left:0}header li:last-child{margin-right:0}.hero{position:relative;overflow:hidden}.hero .content{position:absolute;letter-spacing:2px;text-align:center;width:100%;top:50%;left:50%;transform:translate(-50%,-50%);background:rgba(255,255,255,.7);padding:20px 0}.hero h1{max-width:80%;margin-left:auto;margin-right:auto;font-size:36px}.hero h1.quote{font-size:26px;max-width:60%}@media only screen and (max-width:600px){header{text-align:center}.hero h1.quote{font-size:24px;max-width:80%}}.hero h2{font-size:20px}.hero .social-icons .icon{display:inline-block;width:20px;height:20px;margin:0 10px}@media only screen and (max-width:600px){.hero .social-icons .icon{width:30px;height:30px}}.hero .social-icons svg{width:inherit;height:inherit}.hero .social-icons .twitter{fill:#5EA9DD}.hero .social-icons .linkedin{fill:#0077B5}.paintboard{width:100%;height:100%}body,h1,h2{margin:0}ul{padding:0}li{list-style-type:none}a,a:link,a:visited{color:inherit;text-decoration:none}.animate-link:hover{cursor:default;transition:border-color .5s ease-in 0s;border-color:rgba(0,0,0,.2)}